// Flex
.flex {
  display: flex;
}
.flex.flex-column {
  flex-direction: column;
}
.flex.inline-flex {
  display: inline-flex;
}
.align-self-center {
  align-self: center;
}
.self-end {
  align-self: flex-end;
}
.flex.items-center {
  align-items: center;
}
.flex.justify-center {
  justify-content: center;
}
.flex.justify-end {
  justify-content: flex-end;
}
.flex.justify-between {
  justify-content: space-between;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-column-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

// List
.clean-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

// Text
.vertical-text {
  writing-mode: tb-rl;
  transform: rotate(-180deg);
}

// Aspect ratios

.ratio-16-9 {
  @include aspect-ratio(100, 56);
}

.ratio-square {
  @include aspect-ratio(100, 100);
}

.ratio-card {
  @include aspect-ratio(100, 80);
}

.ratio-poster {
  @include aspect-ratio(100, 150);
}

.grid {
  display: grid;
}

.btn {
  border: none;
  background-color: transparent;
  font-family: Roboto-Regular;
  cursor: pointer;
}

