.login {
  width: 100vw;
  height: 100%;

  &__inner {
    height: 600px;

    .login-form {
      text-align: center;

      input,
      button {
        padding: 0.9rem 1rem;
        margin: 16px 0px 8px;
        vertical-align: top;
        width: 100%;
        background-color: $light-color4;
        font-size: rem(15px);
        outline-color: $primary-color;
      }

      button {
        background-color: $primary-color;
        margin-top: 1.5rem;
        padding: 0.8rem 0.8rem;
        border: none;
        font-size: 17px;
        color: $light-color1;
        font-weight: 600;
        cursor: pointer;
        border: 1px solid $primary-color;

        &:hover {
          background-color: #fff;
          color: $primary-color;
        }
      }

      input {
        border: 1px solid $light-color5;
      }

      h2 {
        font-size: rem(40px);
        letter-spacing: 0;
        margin-bottom: rem(18px);
      }
    }

    p {
      margin-top: rem(12px);
      text-decoration: underline;
      font-size: rem(15px);
      cursor: pointer;

      &:hover {
        font-weight: 700;
      }
    }

    a {
      cursor: pointer;
    }
  }

}

.login-modal,
.signup-modal {
  height: rem(300px);
  margin: 0 auto;
  justify-content: center;

  .login-form-title {
    font-size: rem(25px);
  }

  input {
    background-color: $light-color3;
    border: #ccc 1px solid;
    border-bottom: #ccc 2px solid;
    padding: 7px;
    outline-color: #be123c;

    width: rem(300px);
    color: #aaaaaa;
    margin-top: 10px;
    font-size: 1em;
    border-radius: 4px;
  }

  button {
    background: $primary-color;
    width: rem(300px);
    color: white;
    padding: rem(6px);
    border-radius: 4px;
    border: #be123d83 1px solid;
    cursor: pointer;
    margin-top: 20px;
    margin-bottom: 20px;
    float: left;
    font-weight: 400;
    font-size: 1.2em;

    &:hover {
      background-color: #840626;
    }
  }
}

.choose-site-name {
  margin: 7em auto;

  .ant-modal-content {
    border-radius: 12px;
    background-color: light-color1;
    padding: rem(34px) rem(18px);

    .ant-modal-footer {
      display: none;
    }
  }

  .choose-name {
    width: 100%;

    label {
      color: dark-color1;
      margin-bottom: rem(10px);
      font-size: rem(28px);
      font-weight: 500;
      font-family: Roboto-Medium, sans-serif;
    }

    input {
      background-color: $light-color1;
      border: #ccc 1px solid;
      padding: rem(6px) rem(12px);
      outline-color: $primary-color;
      margin-bottom: rem(7px);
      color: $dark-color1;
      margin-top: 10px;
      font-size: 1em;
      border-radius: 4px;
      font-size: rem(16px);

      &::placeholder {
        font-size: rem(16px);
        color: $dark-color;
      }
    }

    button {
      background-image: linear-gradient(to right bottom, $primary-color );
      font-size: rem(18px);
      padding: rem(5px) rem(24px);
      color: white;
      border-radius: 4px;
      border: $primary-color 1px solid;
      cursor: pointer;
      margin-top: 20px;
      font-weight: 400;

      &:hover {
        background-color: #840625da;
      }
    }
  }
}